import React, { useState, useEffect } from "react";
import {
  Row,
  Col,
  Input,
  Button,
  message,
  Table,
  Select,
  Popover,
  Divider,
  Tag,
} from "antd";
import { QuestionCircleTwoTone } from "@ant-design/icons";
import * as _ from "lodash";
// import { getJobsByjob_nameApi } from "../net/netJob";
import "../component/watchJobInfoPanel.css";
import { global_var } from "../global/userInfo";
import {
  get_candidate_info_api,
  set_deliver_status_api,
} from "../net/netAdmin";
import { downloadResByFileName } from "../net/netDownloadFile";
import { get_resume_file_name_api } from "../net/netUser";
import { time } from "console";
import { select_job_by_id_api } from "../net/netJob";

const { Option } = Select;

// 比较重要的列 姓名 专业 最高学历 性别 毕业时间 到岗时间 所在地
// resume_name  resume_late_major  resume_highest_degree  resume_sex  resume_graduation_date  resume_expected_arrival_time  resume_location
let name_chinese = {
  // input 字段
  query_job_id: "岗位id",
  select_job_id: "岗位id",
  resume_name: "姓名",
  resume_school: "学校名称",
  resume_tele: "电话",
  resume_email: "电子邮箱",
  resume_location: "所在地",
  resume_late_major: "专业",
  resume_highest_degree: "最高学历",
  resume_sex: "性别",

  // 时间选择器
  resume_birthday: "出生年月",
  resume_expected_arrival_time: "到岗时间",
  resume_graduation_date: "毕业时间",

  // text
  resume_skill: "能力",
  resume_award: "曾获奖励",
  resume_self_description: "自我介绍",
  resume_project_experience: "项目经验",
  resume_internship: "实习经历",
  resume_job_intension: "求职意向",
  candidate_detail: "候选人信息",
};

export let resume_info_status_view = (f) => {
  let selects_ = ["", "进行中", "结束"];
  let select_list = selects_.map((item) => {
    return (
      <Option key={item} value={item}>
        {item === "" ? "所有" : item}
      </Option>
    );
  });

  return (
    <Row>
      <Divider dashed={true} type="vertical"></Divider>
      <Tag color="red"> {"招聘状态"} </Tag>
      <Select
        defaultValue=""
        style={{ width: 120 }}
        onChange={(value) => {
          f(value);
        }}
      >
        {select_list}
      </Select>
      <Divider dashed={true} type="vertical"></Divider>
    </Row>
  );
};

// {"code": "0", "message": "", "data": [{"model": "trainModel.jobinfo", "pk": 18, "fields": {"jobuuid": "a783b09eeffb11ea9c180871908061a4", "jobstatus": "未开始", "job_category": "20200906-12:44:32", "starttime": "_", "endtime": "_", "savedir": "C:\\gitproj\\autotrain\\myServer\\storeFiles\\a783b09eeffb11ea9c180871908061a4", "csvname": "mnist_train.csv", "csvsize": "110 MB", "job_name": "asasa12", "modelsize": "0", "modelstructure": "0", "modeltotalsample": "0", "modeltrainsample": "0", "modeltestsample": "0", "modelvalidasample": "0"}},

// 支持 按照 发布时间 ,职位名称，行业类别 , 最低学历
// 公司名称,  >= 最低薪水 , <=最高薪水 进行搜索
export let MangeDeliverInfoPanel = () => {
  // 前端返回的数据
  let [dataS, setDataS] = useState([]);
  //
  let [job_name, set_job_name] = useState("未查询");

  // 查询的变量
  let [query_job_id, set_query_job_id] = useState("");
  let [deliver_status, set_deliver_status] = useState("申请中");

  let [page_index, set_page_index] = useState("1");
  let [sum_page_index, set_sum_page_index] = useState("1");

  async function loadData() {
    let params = {
      query_job_id: query_job_id,
      page_index: page_index,
      deliver_status: deliver_status,
    };
    let data: any = await get_candidate_info_api(params);
    // console.log("回传的 select_job_api data", data);
    setDataS(_.get(data, "data.lists", []));
    set_sum_page_index(_.get(data, "data.sum_page"));
  }

  let maxHeight = document.body.clientHeight * 0.9;

  // 初始化 任务id
  useEffect(() => {
    let time0 = setInterval(() => {
      let query_id = _.get(global_var, "admin_info.query_job_id", "-1");
      console.log("获取到的 全局变量", query_id);
      if (!_.isEqual(query_job_id, query_id)) {
        set_query_job_id(query_id);
      }
    }, 1000);
    return () => {
      window.clearInterval(time0);
    };
  }, []);

  useEffect(() => {
    let reflash_job_name = async () => {
      if (!_.isEqual("", query_job_id)) {
        let params = { job_id: query_job_id };
        let job_data = await select_job_by_id_api(params);
        console.log("前段返回的查询信息", job_data);
        set_job_name(_.get(job_data, "data[0].fields.job_name", "岗位id错误"));
      } else {
        set_job_name("所有岗位");
      }
      loadData();
    };
    reflash_job_name();
  }, [query_job_id]);

  // 监听 分页器 变化
  useEffect(() => {
    if (page_index === "") {
      page_index = "0";
    }
    if (parseInt(page_index) < 1) {
      message.error("页号必须 >1");
      set_page_index("1");
      return;
    } else if (parseInt(page_index) > parseInt(sum_page_index)) {
      message.error("页号不能溢出");
      set_page_index(sum_page_index);
      return;
    } else if (
      parseInt(page_index) < 1 ||
      parseInt(page_index) > parseInt(sum_page_index)
    ) {
      message.error("错误的页号");
      setDataS([]);
      return;
    }
    loadData();
  }, [page_index, deliver_status]);

  let job_id_input_type_view = (column_name) => {
    return (
      <Row>
        <Col>
          <Input
            addonBefore={name_chinese[`${column_name}`]}
            value={eval(column_name)}
            onChange={(event) => {
              let textValue = event.target.value;
              global_var.admin_info.query_job_id = textValue;
              // let query_id = _.get(global_var, "admin_info.query_job_id", "-1");
              eval(`set_${column_name}`)(textValue);
            }}
          />
        </Col>
        <Divider dashed={true} type="vertical"></Divider>
      </Row>
    );
  };

  let select_deliver_status_view = (c_name) => {
    let selects_ = ["", "申请中", "淘汰", "通过"];
    let select_list = selects_.map((item) => {
      return (
        <Option key={item} value={item}>
          {item === "" ? "所有" : item}
        </Option>
      );
    });

    return (
      <Row>
        <Divider dashed={true} type="vertical"></Divider>
        <Tag color="red"> {"投递状态"} </Tag>
        <Select
          defaultValue="申请中"
          style={{ width: 120 }}
          onChange={(value) => {
            eval(`set_${c_name}`)(value);
          }}
        >
          {select_list}
        </Select>
        <Divider dashed={true} type="vertical"></Divider>
      </Row>
    );
  };

  let filiter_inpus_view = () => {
    return (
      <Row gutter={10} justify="center">
        <Col>
          <Row style={{ fontSize: "larger" }}>
            {job_id_input_type_view("query_job_id")}
            <Divider type="vertical"></Divider>
            <Tag color="red" style={{ fontSize: "larger" }}>
              岗位名称
            </Tag>
            <Tag style={{ fontSize: "larger" }}> {job_name} </Tag>
            <Divider type="vertical"></Divider>
            {select_deliver_status_view("deliver_status")}
            <Button
              type="primary"
              onClick={() => {
                loadData();
              }}
            >
              执行查询
            </Button>
          </Row>
        </Col>
      </Row>
    );
  };

  //   fields:
  // resume_award: "编码"
  // resume_birthday: "2020-10"
  // resume_email: "734449600@qq.com"
  // resume_expected_arrival_time: "2020-10"
  // resume_graduation_date: "2020-10"
  // resume_highest_degree: "本科"
  // resume_internship: "阿里巴巴↵1↵2↵12↵"
  // resume_job_intension: "在北京工作↵12↵2↵12↵1↵2"
  // resume_late_major: "计算机科学"
  // resume_location: "河南鹤壁"
  // resume_name: "李孟凯"
  // resume_project_experience: "热爱↵121↵1↵21↵2↵"
  // resume_school: "石家庄铁道大学"
  // resume_self_description: "开朗"
  // resume_sex: "男"
  // resume_skill: "开发能力"
  // resume_tele: "18231129677"
  // resume_uuid: "95e359f0044611eb93f78030491cc416"
  // user_uuid: "d7026be802be11eb82c08030491cc416"

  // 比较重要的列 姓名 专业 最高学历 性别 毕业时间 到岗时间 所在地
  // resume_name  resume_late_major  resume_highest_degree  resume_sex  resume_graduation_date  resume_expected_arrival_time  resume_location
  const columns = [
    {
      title: name_chinese["resume_name"],
      dataIndex: ["fields", "resume_name"],
    },
    {
      title: name_chinese["resume_school"],
      dataIndex: ["fields", "resume_school"],
    },
    {
      title: name_chinese["resume_late_major"],
      dataIndex: ["fields", "resume_late_major"],
    },
    {
      title: name_chinese["resume_highest_degree"],
      dataIndex: ["fields", "resume_highest_degree"],
    },
    {
      title: name_chinese["resume_sex"],
      dataIndex: ["fields", "resume_sex"],
    },
    {
      title: name_chinese["resume_graduation_date"],
      dataIndex: ["fields", "resume_graduation_date"],
    },
    {
      title: name_chinese["resume_expected_arrival_time"],
      dataIndex: ["fields", "resume_expected_arrival_time"],
    },
    {
      title: name_chinese["resume_location"],
      dataIndex: ["fields", "resume_location"],
    },
    {
      title: "详细信息",
      key: "candidate_detail",
      render: (text: any, record: any, index: number) => {
        // console.log("record is", record);

        let detail_view = () => {
          let detail_info_view = (c_name: any) => {
            let c_name_path = `fields.${c_name}`;
            let text_value = _.get(record, c_name_path, "不存在该字段");

            return _.isEqual("不存在该字段", text_value) ? (
              <Col key={c_name}></Col>
            ) : (
              <Row key={c_name}>
                <Col>
                  <Row>
                    <Col>
                      <Tag style={{ fontSize: "larger" }}>
                        {name_chinese[c_name]}
                      </Tag>
                    </Col>
                    <Row>
                      <pre style={{ fontSize: "larger" }}>{text_value}</pre>
                    </Row>
                  </Row>
                  <Divider></Divider>
                </Col>
              </Row>
            );
          };
          let left_info_columns = Object.keys(name_chinese).map(
            (c_name: string, index: number) => {
              return index % 2 == 0 ? (
                detail_info_view(c_name)
              ) : (
                <Col key={c_name}></Col>
              );
            }
          );
          let right_info_columns = Object.keys(name_chinese).map(
            (c_name: string, index: number) => {
              return index % 2 == 1 ? (
                detail_info_view(c_name)
              ) : (
                <Col key={c_name}></Col>
              );
            }
          );

          return (
            <Row>
              <Col>{left_info_columns}</Col>
              <Divider type="vertical"></Divider>
              <Col>{right_info_columns}</Col>
            </Row>
          );
        };
        return (
          <Popover
            key="candidate_detail"
            trigger="hover"
            placement="left"
            title={name_chinese["candidate_detail"]}
            content={detail_view()}
          >
            <Button icon={<QuestionCircleTwoTone />}></Button>
          </Popover>
        );
      },
    },
    {
      // 设置是否通过应聘者
      title: "设置状态",
      key: "modelRes",
      render: (text: any, record: any, index: number) => {
        let job_uuid = _.get(record, "fields.job_uuid", "");
        let user_uuid = _.get(record, "fields.user_uuid", "");
        console.log("job_uuid ", record);
        console.log("user_uuid ", user_uuid);
        return (
          <Row>
            <Button
              key="start_resume"
              type="primary"
              onClick={async () => {
                let params = {
                  job_id: query_job_id,
                  user_uuid: user_uuid,
                  deliver_status: "通过",
                };
                let data = await set_deliver_status_api(params);
                loadData();
              }}
            >
              通过
            </Button>
            <Button
              key="stop_resume"
              type="dashed"
              onClick={async () => {
                let params = {
                  job_id: query_job_id,
                  user_uuid: user_uuid,
                  deliver_status: "淘汰",
                };
                let data = await set_deliver_status_api(params);
                loadData();
              }}
            >
              淘汰
            </Button>
          </Row>
        );
      },
    },

    {
      title: "下载简历",
      key: "delivery_record",
      render: (text: any, record: any, index: number) => {
        // console.log("record is", record);

        let viewContent = () => {
          return <Row>下载应聘者简历</Row>;
        };
        return (
          <Popover
            key="detailInfoPopover"
            trigger="hover"
            placement="left"
            title={name_chinese["job_demand_"]}
            content={viewContent()}
          >
            <Button
              onClick={async () => {
                let get_file_name = async () => {
                  let params = {
                    user_uuid: _.get(record, "fields.user_uuid", "null"),
                  };
                  let data = await get_resume_file_name_api(params);
                  console.log("=remote file name", data);
                  return _.get(data, "data.resume_file_name", "未提交简历");
                };
                let remote_file_name = await get_file_name();
                downloadResByFileName(
                  _.get(record, "fields.user_uuid", "null"),
                  remote_file_name
                );
              }}
              type="primary"
              style={{ fontSize: "larger" }}
            >
              下载
            </Button>
          </Popover>
        );
      },
    },
  ];

  let page_index_view = () => {
    return (
      <Row>
        <Tag style={{ fontSize: "larger" }}>共 {sum_page_index} 页</Tag>
        <Divider type="vertical"></Divider>
        <Button
          onClick={() => {
            let index_ = parseInt(page_index) - 1;
            set_page_index(index_.toString());
          }}
        >
          上一页
        </Button>
        <Divider type="vertical"></Divider>
        <Button
          onClick={() => {
            let index_ = parseInt(page_index) + 1;
            set_page_index(index_.toString());
          }}
        >
          下一页
        </Button>
        <Divider type="vertical"></Divider>
        <Col>
          <Input
            addonBefore="跳转至"
            style={{ width: "200px" }}
            type="number"
            value={page_index}
            onChange={(e) => {
              let textValue = e.target.value;
              set_page_index(textValue);
            }}
          />
        </Col>
        <Divider type="vertical"></Divider>
      </Row>
    );
  };

  return (
    <Row
      gutter={10}
      style={{
        width: "100%",
        minHeight: maxHeight,
        display: "flex",
        justifyContent: "center",
      }}
    >
      <Col>
        <Row gutter={10} justify="center">
          根据招聘 岗位id 查看投递人 , 不输入任务id 则查询所有职位投递人
        </Row>
        {filiter_inpus_view()}
        <Divider type="horizontal" dashed={true}></Divider>
        {page_index_view()}
        <Table
          rowKey={(record) => _.get(record, "pk", "x")}
          dataSource={dataS}
          columns={columns}
          pagination={{ hideOnSinglePage: true }}
        />
        <Divider type="horizontal" dashed={true}></Divider>
      </Col>
    </Row>
  );
};
