import React, { useEffect, useState } from "react";
import {
  Button,
  Card,
  Col,
  Divider,
  Input,
  Row,
  Tag,
  message,
  Select,
  DatePicker,
} from "antd";
import { publish_job_api } from "../net/netJob";
import * as _ from "lodash";
import { global_var } from "../global/userInfo";
import "moment/locale/zh-cn";
import locale from "antd/es/date-picker/locale/zh_CN";
import {
  get_user_resume_info_api,
  user_upload_resume_api,
} from "../net/netUser";
import moment from "moment";
import { UploadResumeFilePanel } from "./uploadResumeFilePanel";

const { TextArea } = Input;
const { Option } = Select;

