let name_chinese = {
  resume_name: "姓名",
  resume_school: "学校名称",
  resume_tele: "电话",
  resume_email: "电子邮箱",
  resume_age: "年龄",
  resume_highest_degree: "最高学历",
  resume_location: "所在地",
  resume_late_major: "专业",
  resume_birthday: "出生年月",
  resume_expected_arrival_time: "到岗时间",
  resume_skill: "能力",
  resume_award: "曾获奖励",
  resume_graduation_date: "毕业时间",

  resume_self_description: "自我介绍",

  resume_project_experience: "项目经验",
  resume_internship: "实习经历",
  resume_job_intension: "求职意向",
  resume_sex: "性别",
};

export let FillSelfInfoPanel = () => {
  let get_std_month_str = () => {
    return moment().format("YYYY-MM");
  };
  let [resume_name, set_resume_name] = useState("");
  let [resume_school, set_resume_school] = useState("");
  let [resume_tele, set_resume_tele] = useState("");
  let [resume_highest_degree, set_resume_highest_degree] = useState("本科");
  let [resume_location, set_resume_location] = useState("");
  let [resume_late_major, set_resume_late_major] = useState("");

  let [resume_skill, set_resume_skill] = useState("");
  let [resume_award, set_resume_award] = useState("");

  let [resume_self_description, set_resume_self_description] = useState("");
  let [resume_project_experience, set_resume_project_experience] = useState("");
  let [resume_internship, set_resume_internship] = useState("");
  let [resume_job_intension, set_resume_job_intension] = useState("");
  let [resume_sex, set_resume_sex] = useState("男");
  let [resume_email, set_resume_email] = useState("");

  let [resume_graduation_date, set_resume_graduation_date] = useState(
    get_std_month_str()
  );
  let [resume_birthday, set_resume_birthday] = useState(get_std_month_str());
  let [
    resume_expected_arrival_time,
    set_resume_expected_arrival_time,
  ] = useState(get_std_month_str());

  let [fill_all_info, set_fill_all_info] = useState(true);

  let get_user_info = () => {
    let params = {
      user_uuid: _.get(global_var, "user_info.user_uuid", "null"),
      resume_name: resume_name,
      resume_school: resume_school,
      resume_tele: resume_tele,
      resume_highest_degree: resume_highest_degree,
      resume_location: resume_location,
      resume_late_major: resume_late_major,
      resume_birthday: resume_birthday,
      resume_expected_arrival_time: resume_expected_arrival_time,
      resume_skill: resume_skill,
      resume_award: resume_award,
      resume_graduation_date: resume_graduation_date,
      resume_self_description: resume_self_description,
      resume_project_experience: resume_project_experience,
      resume_internship: resume_internship,
      resume_job_intension: resume_job_intension,
      resume_email: resume_email,
      resume_sex: resume_sex,
    };
    return params;
  };

  let set_user_info = (d_: any) => {
    set_resume_name(_.get(d_, "resume_name", ""));
    set_resume_school(_.get(d_, "resume_school", ""));
    set_resume_tele(_.get(d_, "resume_tele", ""));
    set_resume_highest_degree(_.get(d_, "resume_highest_degree", "本科"));
    set_resume_location(_.get(d_, "resume_location", ""));
    set_resume_late_major(_.get(d_, "resume_late_major", ""));
    set_resume_birthday(_.get(d_, "resume_birthday", ""));
    set_resume_expected_arrival_time(
      _.get(d_, "resume_expected_arrival_time", "")
    );
    set_resume_skill(_.get(d_, "resume_skill", ""));
    set_resume_award(_.get(d_, "resume_award", ""));
    set_resume_graduation_date(_.get(d_, "resume_graduation_date", ""));
    set_resume_self_description(_.get(d_, "resume_self_description", ""));
    set_resume_project_experience(_.get(d_, "resume_project_experience", ""));
    set_resume_internship(_.get(d_, "resume_internship", ""));
    set_resume_job_intension(_.get(d_, "resume_job_intension", ""));
    set_resume_email(_.get(d_, "resume_email", ""));
    set_resume_sex(_.get(d_, "resume_sex", "男"));
  };

  useEffect(() => {
    let get_user_resume_info = async () => {
      let params = { user_uuid: _.get(global_var, "user_info.user_uuid", "") };
      let data = await get_user_resume_info_api(params);
      let resume_info = _.get(data, "data.[0].fields", {});
      console.log("resume_info", resume_info);
      if (!_.isEqual({}, resume_info)) {
        set_user_info(resume_info);
      }
    };
    get_user_resume_info();
  }, []);

  useEffect(() => {
    change_fill_all_info_status();
  });

  let change_fill_all_info_status = () => {
    let params = get_user_info();
    set_fill_all_info(true);
    Object.keys(params).map((i: string) => {
      if (params[i] === "") {
        set_fill_all_info(false);
        return;
      }
    });
  };

  let select_education_view = (c_name: string) => {
    let min_education_selects = [
      "大专",
      "本科",
      "硕士",
      "博士",
      "中专",
      "高中",
      "初中",
      "小学",
      "其他",
      "",
    ];
    let select_list = min_education_selects.map((item) => {
      return (
        <Option key={item} value={item}>
          {item == "" ? "不限" : item}
        </Option>
      );
    });

    return (
      <Row>
        <Col>
          <Row>
            <Tag color="red"> {name_chinese[c_name]} </Tag>
            <Select
              value={eval(c_name)}
              style={{ width: 120 }}
              onChange={(value) => {
                eval(`set_${c_name}`)(value);
              }}
            >
              {select_list}
            </Select>
          </Row>
          <Divider></Divider>
        </Col>
      </Row>
    );
  };

  let select_sex_view = (c_name: string) => {
    let min_education_selects = ["男", "女"];
    let select_list = min_education_selects.map((item) => {
      return (
        <Option key={item} value={item}>
          {item == "" ? "不限" : item}
        </Option>
      );
    });

    return (
      <Row>
        <Col>
          <Row>
            <Tag color="red"> {name_chinese[c_name]} </Tag>
            <Select
              value={eval(c_name)}
              style={{ width: 120 }}
              onChange={(value) => {
                eval(`set_${c_name}`)(value);
              }}
            >
              {select_list}
            </Select>
          </Row>
          <Divider></Divider>
        </Col>
      </Row>
    );
  };

  let input_from_column = (c_name: string) => {
    return (
      <Row key={c_name}>
        <Input
          addonBefore={_.get(name_chinese, c_name, "找不到")}
          value={eval(c_name)}
          onChange={(e) => {
            let text = e.target.value;
            eval(`set_${c_name}`)(text);
          }}
        />
        <Divider dashed={true}> </Divider>
      </Row>
    );
  };

  let text_area_from_column = (c_name: string) => {
    let auto_config = { minRows: 5 };
    return (
      <Col key={c_name}>
        <Divider></Divider>
        <Tag color="blue"> {name_chinese[c_name]} </Tag>
        <TextArea
          autoSize={auto_config}
          value={eval(c_name)}
          onChange={(event) => {
            let default_len = 1000;
            let textValue = event.target.value;

            if (textValue.length > default_len) {
              let config = {
                content: (
                  <Row>
                    {name_chinese[c_name]} 不能超过 {default_len} 位
                  </Row>
                ),
                duration: 3,
              };
              message.error(config);
            } else {
              eval(`set_${c_name}`)(textValue);
            }
          }}
        />
      </Col>
    );
  };

  let time_picker_from_column = (c_name) => {
    let temp_date = eval(c_name) === "" ? moment() : moment(eval(c_name));
    return (
      <Col key={c_name}>
        <Row>
          <Tag color="geekblue">{name_chinese[c_name]}</Tag>
          <DatePicker
            value={temp_date}
            locale={locale}
            onChange={(date, datestring) => {
              eval(`set_${c_name}`)(datestring);
            }}
            picker="month"
          />
        </Row>

        <Divider></Divider>
      </Col>
    );
  };

  // let maxHeight = document.body.clientHeight * 0.9;

  let left_name_list = [
    "resume_name",
    "resume_tele",
    "resume_email",
    "resume_location",
    "resume_school",
    "resume_late_major",
  ];
  // let right_name_list = [];

  let text_area_name_list = [
    "resume_self_description",
    "resume_skill",
    "resume_award",
    "resume_project_experience",
    "resume_internship",
    "resume_job_intension",
  ];

  let time_picker_name_list = [
    "resume_graduation_date",
    "resume_birthday",
    "resume_expected_arrival_time",
  ];
  let leftInputView = () => {
    let left_view_list = left_name_list.map((value) => {
      let temp = value;
      return input_from_column(temp);
    });
    return <Col>{left_view_list}</Col>;
  };
  // let rightInputView = () => {
  //   let view_list = right_name_list.map((value) => {
  //     let temp = value;
  //     return input_from_column(temp);
  //   });
  //   return <Col>{view_list}</Col>;
  // };

  let textAreaInputAreaView = () => {
    let view_list = text_area_name_list.map((value) => {
      let temp = value;
      return text_area_from_column(temp);
    });
    return <Col>{view_list}</Col>;
  };

  let timePickerView = () => {
    let view_list = time_picker_name_list.map((value) => {
      let temp = value;
      return time_picker_from_column(temp);
    });
    return <Col>{view_list}</Col>;
  };

  let fill_info_view = () => {
    return (
      <Col>
        <Card>
          <Col>
            <Row>
              <Col>{leftInputView()}</Col>
              <Divider type="vertical" dashed={true}></Divider>
              <Col>
                {select_sex_view("resume_sex")}
                {select_education_view("resume_highest_degree")}
                {/* {rightInputView()} */}
                {timePickerView()}
              </Col>
            </Row>
            {textAreaInputAreaView()}
            <Divider dashed={true}></Divider>
            <Button
              type="primary"
              disabled={!fill_all_info}
              onClick={async () => {
                let params = get_user_info();
                let data = await user_upload_resume_api(params);
                if (_.isEqual("0", _.get(data, "code"))) {
                  message.success("成功更新基本信息");
                }
              }}
            >
              {fill_all_info ? "更新个人信息" : "部分信息未填写"}
            </Button>
          </Col>
        </Card>
      </Col>
    );
  };

  return (
    <Row gutter={10} justify="center" style={{ width: "100%" }}>
      <Col flex={1}>
        <UploadResumeFilePanel />
      </Col>
      <Col flex={1}>{fill_info_view()}</Col>
    </Row>
  );
};
