import React, { useState, useEffect } from "react";
import { Row } from "antd";
import { Route } from "react-router-dom";
import { MainPanel } from "./mainPanel";
import { LogInPanel } from "./logInPanel";
import { SignInPanel } from "./signInPanel";
import { AdminLogInPanel } from "../adminComponent/adminLogInPanel";
import { AdminMainPanel } from "../adminComponent/adminMainPanel";

export let RouteControl = () => {
  return (
    <Row>
      <Route exact path={"/"}>
        <LogInPanel />
      </Route>
      <Route path={"/signin/"}>
        <SignInPanel />
      </Route>
      <Route path={"/main/"}>
        <MainPanel />
      </Route>
      <Route path={"/admin/"}>
        <AdminLogInPanel />
      </Route>
      <Route path={"/admin_main/"}>
        <AdminMainPanel />
      </Route>
    </Row>
  );
};
