import {
  publish_job_url,
  select_job_by_id_url,
  select_job_url,
  set_job_status_url,
} from "./netConfig";
import { GetRequestCode } from "./netDoRequest";

export let publish_job_api = async (params: any) => {
  let data = await GetRequestCode(publish_job_url, params);
  return data;
};

export let select_job_api = async (params: any) => {
  let data = await GetRequestCode(select_job_url, params);
  return data;
};

export let set_job_status_api = async (params: any) => {
  let data = await GetRequestCode(set_job_status_url, params);
  return data;
};

export let select_job_by_id_api = async (params: any) => {
  let data = await GetRequestCode(select_job_by_id_url, params);
  return data;
};
