import React, { useState, useEffect } from "react";
import {
  Row,
  Col,
  Upload,
  Input,
  Button,
  message,
  Table,
  Select,
  Popover,
  Divider,
  Tag,
} from "antd";
import { AnyCnameRecord } from "dns";
import {
  RightCircleTwoTone,
  QuestionCircleTwoTone,
  DownloadOutlined,
} from "@ant-design/icons";
import * as _ from "lodash";
// import { getJobsByjob_nameApi } from "../net/netJob";
import prettyBytes from "pretty-bytes";
import ReactJson from "react-json-view";
import { duration } from "moment";
import { downloadResByUuid } from "../net/netDownloadFile";
import { select_job_api, set_job_status_api } from "../net/netJob";
import {
  min_education_view,
  name_chinese,
} from "../adminComponent/releaseJobInfoPanel";
import { global_var } from "../global/userInfo";
import { deliver_job_api, get_deliver_status_api } from "../net/netUser";
import { UserDeliverButton } from "../button/userDeliverButton";
import "./watchJobInfoPanel.css";

const { Option } = Select;

export let resume_info_status_view = (f) => {
  let selects_ = ["", "进行中", "结束"];
  let select_list = selects_.map((item) => {
    return (
      <Option key={item} value={item}>
        {item === "" ? "所有" : item}
      </Option>
    );
  });

  return (
    <Row>
      <Divider dashed={true} type="vertical"></Divider>
      <Tag color="red"> {"招聘状态"} </Tag>
      <Select
        defaultValue="进行中"
        style={{ width: 120 }}
        onChange={(value) => {
          f(value);
        }}
      >
        {select_list}
      </Select>
      <Divider dashed={true} type="vertical"></Divider>
    </Row>
  );
};

// {"code": "0", "message": "", "data": [{"model": "trainModel.jobinfo", "pk": 18, "fields": {"jobuuid": "a783b09eeffb11ea9c180871908061a4", "jobstatus": "未开始", "job_category": "20200906-12:44:32", "starttime": "_", "endtime": "_", "savedir": "C:\\gitproj\\autotrain\\myServer\\storeFiles\\a783b09eeffb11ea9c180871908061a4", "csvname": "mnist_train.csv", "csvsize": "110 MB", "job_name": "asasa12", "modelsize": "0", "modelstructure": "0", "modeltotalsample": "0", "modeltrainsample": "0", "modeltestsample": "0", "modelvalidasample": "0"}},

// 支持 按照 发布时间 ,职位名称，行业类别 , 最低学历
// 公司名称,  >= 最低薪水 , <=最高薪水 进行搜索
export let WatchJobInfoPanel = () => {
  // 前端返回的数据
  let [dataS, setDataS] = useState([]);

  // 查询的变量
  let [job_name, set_job_name] = useState("");
  let [job_category, set_job_category] = useState("");
  let [min_education, set_min_education] = useState("");
  let [company_name, set_company_name] = useState("");

  let [min_salary, set_min_salary] = useState("0");
  let [max_salary, set_max_salary] = useState("999999");
  let [job_create_time, set_job_create_time] = useState("");
  let [job_status, set_job_status] = useState("进行中");

  let [page_index, set_page_index] = useState("1");
  let [sum_page_index, set_sum_page_index] = useState("1");

  async function loadData() {
    let params = {
      page_index: page_index,
      job_name: job_name,
      job_category: job_category,
      min_education: min_education,
      company_name: company_name,
      min_salary: min_salary,
      max_salary: max_salary,
      job_create_time: job_create_time,
      job_status: job_status,
    };
    let data: any = await select_job_api(params);
    // console.log("回传的 select_job_api data", data);
    setDataS(_.get(data, "data.lists", []));
    set_sum_page_index(_.get(data, "data.sum_page"));
  }

  useEffect(() => {
    if (page_index === "") {
      page_index = "0";
    }
    if (parseInt(page_index) < 1) {
      message.error("页号必须 >1");
      set_page_index("1");
      return;
    } else if (parseInt(page_index) > parseInt(sum_page_index)) {
      message.error("页号不能溢出");
      set_page_index(sum_page_index);
      return;
    } else if (
      parseInt(page_index) < 1 ||
      parseInt(page_index) > parseInt(sum_page_index)
    ) {
      message.error("错误的页号");
      setDataS([]);
      return;
    }
    loadData();
  }, [page_index, job_status, min_education]);

  let input_type_view = (column_name) => {
    return (
      <Row>
        <Col>
          <Input
            addonBefore={name_chinese[`${column_name}_`]}
            value={eval(column_name)}
            onChange={(event) => {
              let textValue = event.target.value;
              eval(`set_${column_name}`)(textValue);
            }}
          />
        </Col>
        <Divider dashed={true} type="vertical"></Divider>
      </Row>
    );
  };

  let filiter_inpus_view = () => {
    return (
      <Row gutter={10} justify="center">
        <Col>
          <Row>
            {input_type_view("job_name")}
            {input_type_view("job_category")}
            {input_type_view("job_create_time")}
            {input_type_view("company_name")}
          </Row>
          <Row>
            {input_type_view("min_salary")}
            {input_type_view("max_salary")}
            {min_education_view("", set_min_education)}
            {resume_info_status_view(set_job_status)}
            <Col>
              <Button
                type="primary"
                onClick={() => {
                  loadData();
                }}
              >
                执行查询
              </Button>
            </Col>
          </Row>
        </Col>
      </Row>
    );
  };

  // "fields":{14 items
  //   "job_name":string"12"
  //   "job_category":string""
  //   "min_education":string""
  //   "min_salary":int1212
  //   "max_salary":int343423
  //   "job_describe":string""
  //   "company_name":string""
  //   "job_demand":string""
  //   "company_tele":string""
  //   "company_email":string""
  //   "job_uuid":string"6473d957007011eb87498030491cc416"
  //   "job_create_time":string"20200927-11:20:28"
  //   "job_end_time":string""
  //   "job_status":string"进行中"
  //   }

  const columns = [
    {
      title: "招聘id",
      dataIndex: "pk",
    },
    {
      title: name_chinese["job_name_"],
      dataIndex: ["fields", "job_name"],
    },
    {
      title: name_chinese["job_category_"],
      dataIndex: ["fields", "job_category"],
    },
    {
      title: name_chinese["min_education_"],
      dataIndex: ["fields", "min_education"],
    },
    {
      title: name_chinese["company_name_"],
      dataIndex: ["fields", "company_name"],
    },
    {
      title: name_chinese["min_salary_"],
      dataIndex: ["fields", "min_salary"],
    },
    {
      title: name_chinese["max_salary_"],
      dataIndex: ["fields", "max_salary"],
    },
    {
      title: name_chinese["job_status_"],
      dataIndex: ["fields", "job_status"],
    },
    {
      title: name_chinese["job_describe_"],
      key: "job_describe",
      render: (text: any, record: any, index: number) => {
        // console.log("record is", record);
        let show_text = _.get(record, "fields.job_describe", "");
        let viewContent = () => {
          return (
            <Row>
              <pre>{show_text === "" ? "招聘方未填写" : show_text}</pre>
            </Row>
          );
        };
        return (
          <Popover
            key="detailInfoPopover"
            trigger="hover"
            placement="left"
            title={name_chinese["job_describe_"]}
            content={viewContent()}
          >
            <Button icon={<QuestionCircleTwoTone />}></Button>
          </Popover>
        );
      },
    },
    {
      title: name_chinese["job_demand_"],
      key: "job_demand",
      render: (text: any, record: any, index: number) => {
        // console.log("record is", record);
        let show_text = _.get(record, "fields.job_demand", "");
        let viewContent = () => {
          return (
            <Row>
              <pre> {show_text === "" ? "招聘方未填写" : show_text}</pre>
            </Row>
          );
        };
        return (
          <Popover
            key="detailInfoPopover"
            trigger="hover"
            placement="left"
            title={name_chinese["job_demand_"]}
            content={viewContent()}
          >
            <Button icon={<QuestionCircleTwoTone />}></Button>
          </Popover>
        );
      },
    },
    {
      title: "投递岗位",
      key: "delivery_resume",
      render: (text: any, record: any, index: number) => {
        // console.log("record is", record);
        let params = {
          job_uuid: _.get(record, "fields.job_uuid", "null"),
          user_uuid: _.get(global_var, "user_info.user_uuid", "null"),
        };
        let info = { params: params, record: record };
        return UserDeliverButton(info);
      },
    },
  ];

  let page_index_view = () => {
    return (
      <Row>
        <Tag style={{ fontSize: "larger" }}>共 {sum_page_index} 页</Tag>
        <Divider type="vertical"></Divider>
        <Button
          onClick={() => {
            let index_ = parseInt(page_index) - 1;
            set_page_index(index_.toString());
          }}
        >
          上一页
        </Button>
        <Divider type="vertical"></Divider>
        <Button
          onClick={() => {
            let index_ = parseInt(page_index) + 1;
            set_page_index(index_.toString());
          }}
        >
          下一页
        </Button>
        <Divider type="vertical"></Divider>
        <Col>
          <Input
            addonBefore="跳转至"
            style={{ width: "200px" }}
            type="number"
            value={page_index}
            onChange={(e) => {
              let textValue = e.target.value;
              set_page_index(textValue);
            }}
          />
        </Col>
        <Divider type="vertical"></Divider>
      </Row>
    );
  };

  return (
    <Row gutter={10} justify="center" style={{ width: "100%" }}>
      <Col>
        <Row gutter={10} justify="center">
          {" "}
          模糊匹配搜索部分数据 样例: 日期格式 20200906-12:44:32{" "}
        </Row>
        {filiter_inpus_view()}
        <Divider type="horizontal" dashed={true}></Divider>
        {page_index_view()}
        <Table
          rowKey={(record) => _.get(record, "pk", "x")}
          dataSource={dataS}
          columns={columns}
          pagination={{ hideOnSinglePage: true }}
          rowClassName={(record) => {
            return _.isEqual("结束", _.get(record, "fields.job_status"))
              ? "stop_resume_column"
              : "start_resume_column";
          }}
        />
        <Divider type="horizontal" dashed={true}></Divider>
      </Col>
    </Row>
  );
};
