import { doPostRequest, GetRequestCode } from "./netDoRequest";
import {
  get_user_resume_info_url,
  user_login_url,
  user_register_url,
  user_upload_resume_url,
  upload_resume_file_url,
  get_resume_file_name_url,
  deliver_job_url,
  get_deliver_status_url,
  set_deliver_status_url,
} from "./netConfig";

export let user_register_api = async (params: any) => {
  let data = await GetRequestCode(user_register_url, params);
  return data;
};

export let user_login_api = async (params: any) => {
  let data = await GetRequestCode(user_login_url, params);
  return data;
};

export let user_upload_resume_api = async (params: any) => {
  let data = await GetRequestCode(user_upload_resume_url, params);
  return data;
};

export let get_user_resume_info_api = async (params: any) => {
  let data = await GetRequestCode(get_user_resume_info_url, params);
  return data;
};

export let get_resume_file_name_api = async (params: any) => {
  let data = await GetRequestCode(get_resume_file_name_url, params);
  return data;
};

export let upload_resume_file_api = (fromData: Object) => {
  doPostRequest(upload_resume_file_url, fromData);
};

export let deliver_job_api = async (params: any) => {
  let data = await GetRequestCode(deliver_job_url, params);
};

export let get_deliver_status_api = async (params: any) => {
  let data = await GetRequestCode(get_deliver_status_url, params);
  return data;
};

