import { doGetRequest } from "./netDoRequest";
import { Gproxy, Gtimeout } from "./netConfig";
import { download_url, nginx_download_url } from "./netConfig";
import axios from "axios";
import * as _ from "lodash";
import { message } from "antd";
// import download from "../jsResource/download";
// const download = require("../jsResource/download");

export async function doGetRequestDownFile(url: string, queryData: Object) {
  //  console.log(url);
  let resq = {};
  try {
    resq = await axios.get(Gproxy + url, {
      params: {
        data: JSON.stringify(queryData),
      },
      timeout: Gtimeout,
      responseType: "blob",
    });
    const fileurl = window.URL.createObjectURL(
      new Blob([_.get(resq, "data", "")])
    );
    const link = document.createElement("a");
    link.href = fileurl;
    link.setAttribute("download", _.get(queryData, "file_name", "demo.txt"));
    document.body.appendChild(link);
    link.click();
    message.info("下载成功");
  } catch (err) {
    message.info(err);
  }
}

export let downloadMnist = () => {
  let queryParams = { file_name: "small_mnist_train.csv" };
  doGetRequestDownFile(download_url, queryParams);
};

let downNginxFiles = async (fileurl: string, fileName: string) => {
  let resq = {};

  resq = await axios.get(fileurl, {
    params: {},
    timeout: Gtimeout,
    responseType: "blob",
  });
  fileurl = window.URL.createObjectURL(new Blob([_.get(resq, "data", "")]));
  const link = document.createElement("a");
  link.href = fileurl;
  link.setAttribute("download", fileName);
  document.body.appendChild(link);
  link.click();
  message.info("下载成功");
};

export let downloadResByUuid = (uuid: string) => {
  let downloadUrl = nginx_download_url + uuid.toString() + ".zip";
  downNginxFiles(downloadUrl, uuid.toString() + ".zip");
};

export let downloadResByFileName = (uuid: string, file_name: string) => {
  let downloadUrl = nginx_download_url + uuid.toString() + "/" + file_name;
  downNginxFiles(downloadUrl, file_name);
};
