import React, { useEffect, useState } from "react";
import { Layout, Menu, Row, Typography, message } from "antd";

import * as _ from "lodash";
import SubMenu from "antd/lib/menu/SubMenu";
import { Route, useHistory } from "react-router-dom";
import { RealseJobInfoPanel } from "./releaseJobInfoPanel";
import { MangeJobInfoPanel } from "./mangeJobInfoPanel";
import { check_admin_login_status } from "../component/checkStatus";
import { MangeDeliverInfoPanel } from "./mangeDeliverInfoPanel";
const { Title } = Typography;
const { Header, Content, Footer, Sider } = Layout;

export let AdminMainPanel = () => {
  let [isCollapsed, setIsCollapsed] = useState(false);

  let [jobType, setjobType] = useState("mange_job_info");

  let history = useHistory();

  useEffect(() => {
    history.replace("/admin_main/mange_job_info/");
  }, []);

  useEffect(() => {
    let check_res = async () => {
      let res = await check_admin_login_status();
      if (res == -1) {
        history.replace("/admin");
        message.info("未登录");
      }
    };
    check_res();
  }, []);

  // useEffect(() => {
  //   ShowJobPage();
  // }, [jobType]);

  // let ShowJobPage = () => {
  //   if (_.isEqual("release_job_info", jobType)) {
  //     history.replace("/admin_main/release_job_info/");
  //   } else if (_.isEqual("mange_job_info", jobType)) {
  //     history.replace("/admin_main/mange_job_info/");
  //   } else if (_.isEqual("candidate_record", jobType)) {
  //     history.replace("/admin_main/candidate_record/");
  //   } else if (_.isEqual("watch_self_info", jobType)) {
  //     history.replace("/admin_main/release_job_info/");
  //   }
  // };

  let routeControl = () => {
    return (
      <Row>
        <Route path="/admin_main/release_job_info/">
          <RealseJobInfoPanel />
        </Route>
        <Route path="/admin_main/mange_job_info/">
          <MangeJobInfoPanel />
        </Route>
        <Route path="/admin_main/candidate_record/">
          {MangeDeliverInfoPanel()}
        </Route>
      </Row>
    );
  };

  return (
    <Layout style={{ opacity: 3 }}>
      <Sider
        style={{
          overflow: "auto",
          height: "100vh",
          left: 0,
        }}
        collapsible={true}
        collapsed={isCollapsed}
        onCollapse={() => {
          setIsCollapsed(!isCollapsed);
        }}
      >
        <Menu
          theme="dark"
          defaultSelectedKeys={["mange_job_info"]}
          mode="inline"
        >
          <SubMenu key="resume_menu" title=" 招聘信息 ">
            <Menu.Item
              key="mange_job_info"
              onClick={() => {
                history.replace("/admin_main/mange_job_info/");
              }}
            >
              查看与管理
            </Menu.Item>
            <Menu.Item
              key="release_job_info"
              onClick={() => {
                history.replace("/admin_main/release_job_info/");
              }}
            >
              发布招聘
            </Menu.Item>
          </SubMenu>

          <Menu.Item
            key="candidate_record"
            onClick={() => {
              history.replace("/admin_main/candidate_record/");
            }}
          >
            应聘记录
          </Menu.Item>

          {/* <Menu.Item
            key="regist_record"
            onClick={() => {
              history.replace("/admin_main/regist_record/");
            }}
          >
            注册记录
          </Menu.Item>

          <Menu.Item key="notification" onClick={() => {}}>
            公告通知
          </Menu.Item>
          <Menu.Item key="about_ours" onClick={() => {}}>
            关于我们
          </Menu.Item> */}
          <Menu.Item
            key="quit_login"
            onClick={() => {
              history.replace("/admin");
            }}
          >
            退出登录
          </Menu.Item>
        </Menu>
      </Sider>
      <Layout className="site-layout">
        <Header
          style={{
            lineHeight: "20px",
            borderTop: "0px",
            backgroundColor: "orange",
            fontSize: "20px",
            height: "25px",
          }}
        >
          招聘信息网
        </Header>
        {/* <Content style={{ margin: "24px 16px 0", overflow: "initial" }}> */}
        <Content style={{ margin: "0 16px" }}>{routeControl()}</Content>
        <Footer
          style={{
            textAlign: "center",
            fontSize: "20px",
            backgroundColor: "mintcream",
          }}
        >
          resume web site ©2020 Created by kiki
        </Footer>
      </Layout>
    </Layout>
  );
};
