import React, { useEffect, useState } from "react";
import { Popover, Row, Button, Col, Divider } from "antd";
import {
  deliver_job_api,
  get_deliver_status_api,
  get_resume_file_name_api,
  get_user_resume_info_api,
} from "../net/netUser";
import * as _ from "lodash";
import { global_var } from "../global/userInfo";
import { useHistory } from "react-router";
export let UserDeliverButton = (info: any) => {
  let [button_text, set_button_text] = useState("申请");
  let [resume_is_full, set_resume_is_full] = useState(false);
  let [resume_file_is_upload, set_resume_file_is_upload] = useState(false);

  let params = _.get(info, "params", {});
  let record = _.get(info, "record", {});

  let get_job_status = async () => {
    let data = await get_deliver_status_api(params);
    console.log("这条记录的 投递结果", data);
    let res = _.get(data, "data.deliver_status", "未申请");
    console.log("结果 ", res);
    set_button_text(_.isEqual("未投递", res) ? "投递" : res);
  };
  let get_user_resume_info = async () => {
    let params = { user_uuid: _.get(global_var, "user_info.user_uuid", "") };
    let data = await get_user_resume_info_api(params);
    return _.get(data, "data.[0].fields", {});
  };
  let get_file_name = async () => {
    let params = {
      user_uuid: _.get(global_var, "user_info.user_uuid", "null"),
    };
    let data = await get_resume_file_name_api(params);
    return _.get(data, "data.resume_file_name", "未提交");
  };

  let change_fill_all_info_status = (params) => {
    set_resume_is_full(true);
    if (_.isEqual({}, params)) {
      set_resume_is_full(false);
      return;
    }
    Object.keys(params).map((i: string) => {
      if (params[i] === "") {
        set_resume_is_full(false);
        return;
      }
    });
  };

  let change_resume_file_status = (resume_file_name) => {
    console.log("resume_filename", resume_file_name);
    if (_.isEqual("未提交", resume_file_name)) {
      set_resume_file_is_upload(false);
    } else {
      set_resume_file_is_upload(true);
    }
  };

  let check_resume = async () => {
    let resume_info = await get_user_resume_info();
    let resume_file_name = await get_file_name();
    change_fill_all_info_status(resume_info);
    change_resume_file_status(resume_file_name);
  };

  useEffect(() => {
    get_job_status();
    check_resume();
  }, []);

  let history = useHistory();

  let viewContent = () => {
    let resume_warning_view = () => {
      return (
        <Col>
          <Divider></Divider>

          <Col>简历信息不完善</Col>
          <Col>
            <Button
              type="primary"
              onClick={() => {
                history.replace("/main/fill_self_info/");
              }}
            >
              去补全信息
            </Button>
          </Col>
        </Col>
      );
    };
    let resume_file_warning_view = () => {
      return (
        <Col>
          <Divider></Divider>
          <Col>简历附件未提交</Col>
          <Col>
            <Button
              type="primary"
              onClick={() => {
                history.replace("/main/fill_self_info/");
              }}
            >
              去提交
            </Button>
          </Col>
        </Col>
      );
    };

    let resume_stop_warning_view = () => {
      return (
        <Col>
          <Divider></Divider>
          <Col>招聘已经结束</Col>
        </Col>
      );
    };
    return (
      <Row>
        <Col style={{ fontSize: "larger" }}>
          <Col>上传简历,并完善信息后才可以投递哦</Col>

          <Col>{resume_is_full ? "" : resume_warning_view()}</Col>

          <Col>{resume_file_is_upload ? "" : resume_file_warning_view()}</Col>

          <Col>
            {!_.isEqual("结束", _.get(record, "fields.job_status"))
              ? ""
              : resume_stop_warning_view()}
          </Col>
        </Col>
      </Row>
    );
  };
  return (
    <Popover
      key="detailInfoPopover"
      trigger="hover"
      placement="left"
      title={"投递此岗位"}
      content={viewContent()}
    >
      <Button
        onClick={async () => {
          deliver_job_api(params);
          console.log("记录的 ", record);
          setTimeout(() => {
            get_job_status();
          }, 500);
        }}
        disabled={
          !_.isEqual("投递", button_text) ||
          !resume_is_full ||
          !resume_file_is_upload ||
          _.isEqual("结束", _.get(record, "fields.job_status"))
        }
      >
        {button_text}
      </Button>
    </Popover>
  );
};
