import {
  admin_login_url,
  get_candidate_info_url,
  set_deliver_status_url,
} from "./netConfig";
import { GetRequestCode } from "./netDoRequest";
export let admin_login_api = async (params: any) => {
  let data = await GetRequestCode(admin_login_url, params);
  return data;
};

export let get_candidate_info_api = async (params: any) => {
  let data = await GetRequestCode(get_candidate_info_url, params);
  return data;
};

export let set_deliver_status_api = async (params: any) => {
  let data = await GetRequestCode(set_deliver_status_url, params);
};
