import React, { useState, useCallback, useEffect } from "react";
import { Button, Card, Input, Row, message, Col, Divider, Tag } from "antd";
import { Link, useHistory } from "react-router-dom";
import Captcha from "react-captcha-code";
import * as _ from "lodash";
import ReactCanvasNest from "react-canvas-nest";
import "./logInPanel.css";
import { user_login_api } from "../net/netUser";
import { global_var } from "../global/userInfo";

export let LogInPanel = () => {
  let [user_tele, set_user_tele] = useState("");
  let [user_pwd, set_user_pwd] = useState("");
  let [user_verify_code, set_user_verify_code] = useState("");
  let [verify_code, set_verify_code] = useState("");

  let [tele_len, set_tele_len] = useState(0);
  let [pwd_len, set_pwd_len] = useState(0);
  let [user_verify_code_len, set_user_verify_code_len] = useState(0);

  let history = useHistory();

  const handleClick = useCallback((captcha) => {
    set_verify_code(captcha);
  }, []);

  // 整个 屏幕的 大小
  // let maxHeight = window.screen.availHeight;
  // let maxWidth = window.screen.availWidth;

  //当前 窗口的 大小
  let maxWidth = document.body.clientWidth;
  let maxHeight = document.body.clientHeight;
  let halfWidth = maxWidth / 1.8;
  let halfHeight = maxHeight / 1.8;

  let goTORegistPanel = () => {
    return (
      <Row justify="center">
        <Link to="/signin">
          <Button style={{ fontSize: "larger" }}>
            未注册用户,点击此处进入 注册页面
          </Button>
        </Link>
      </Row>
    );
  };

  let loginView = () => {
    return (
      <Row justify="center">
        <Col>
          {input_tele_col()}
          <Divider dashed={true}></Divider>
          {input_pwd_col()}
          <Divider dashed={true}></Divider>
          {input_verify_code_col()}
          <Divider dashed={true}></Divider>
          <Row>
            <Button
              disabled={!_.isEqual(verify_code, user_verify_code)}
              onClick={async () => {
                if (user_tele.length != 11) {
                  message.info("电话号码不是 11 位");
                  return;
                }
                if (user_pwd.length < 6) {
                  message.info("密码不足 6 位");
                  return;
                }
                let user_info = {
                  user_tele: user_tele,
                  user_pwd: user_pwd,
                };
                let data = await user_login_api(user_info);
                if (_.isEqual("0", _.get(data, "code"))) {
                  global_var["user_info"] = _.get(data, "data", {});
                  history.replace("/main");
                }
              }}
            >
              {_.isEqual(verify_code, user_verify_code)
                ? "验证通过,登录"
                : "验证码错误"}
            </Button>
          </Row>
        </Col>
      </Row>
    );
  };

  let input_tele_col = () => {
    let tele_default_len = 11;
    return (
      <Row>
        <Input
          addonBefore={"电话号码"}
          value={user_tele}
          onChange={(event) => {
            let textValue = event.target.value;
            if (/^[0-9]+$/.test(textValue)) {
              set_user_tele(event.target.value);
              set_tele_len(textValue.length);
            } else {
              set_user_tele("");
              set_tele_len(0);
              let config = {
                content: <Row>电话号码 只能包含数字</Row>,
                duration: 3,
              };
              message.error(config);
            }

            if (textValue.length > tele_default_len) {
              set_user_tele("");
              set_tele_len(0);
              let config = {
                content: <Row>电话号码 不能超过 {tele_default_len} 位</Row>,
                duration: 3,
              };
              message.error(config);
            }
          }}
          placeholder={" 注册时填写的电话号码 "}
          style={{ width: "300px" }}
        ></Input>

        <Divider dashed={true} type="vertical"></Divider>
        <Tag color="blue" style={{ fontSize: "larger" }}>
          {tele_len}
        </Tag>
        <Tag color="green" style={{ fontSize: "larger" }}>
          位
        </Tag>
      </Row>
    );
  };

  let input_pwd_col = () => {
    let pwd_default_len = 20;
    return (
      <Row>
        <Input
          type="password"
          addonBefore={"密码"}
          value={user_pwd}
          onChange={(event) => {
            let textValue = event.target.value;
            if (/^[a-zA-Z0-9!?._#@]+$/.test(textValue)) {
              set_user_pwd(event.target.value);
              set_pwd_len(textValue.length);
            } else {
              set_user_pwd("");
              set_pwd_len(0);
              let config = {
                content: <Row> {"密码 只能包含 a-zA-Z0-9!?._#@ "} </Row>,
                duration: 3,
              };
              message.error(config);
            }

            if (textValue.length > pwd_default_len) {
              set_user_pwd("");
              set_pwd_len(0);
              let config = {
                content: <p>密码 不能超过 {pwd_default_len} 位</p>,
                duration: 3,
              };
              message.error(config);
            }
          }}
          placeholder={" 注册时填写的密码 "}
          style={{ width: "300px" }}
        ></Input>

        <Divider dashed={true} type="vertical"></Divider>
        <Tag color="blue" style={{ fontSize: "larger" }}>
          {pwd_len}
        </Tag>
        <Tag color="green" style={{ fontSize: "larger" }}>
          位
        </Tag>
      </Row>
    );
  };

  let input_verify_code_col = () => {
    let verify_code_default_len = 5;
    return (
      <Row>
        <Input
          addonBefore={"验证码"}
          value={user_verify_code}
          onChange={(event) => {
            let textValue = event.target.value;
            if (/^[a-zA-Z0-9]+$/.test(textValue)) {
              set_user_verify_code(event.target.value);
              set_user_verify_code_len(textValue.length);
            } else {
              set_user_verify_code("");
              set_user_verify_code_len(0);
              let config = {
                content: <Row> {"验证码 只能包含 a-zA-Z0-9 "} </Row>,
                duration: 3,
              };
              message.error(config);
            }

            if (textValue.length > verify_code_default_len) {
              set_user_verify_code("");
              set_user_verify_code_len(0);
              let config = {
                content: <p>密码 不能超过 {verify_code_default_len} 位</p>,
                duration: 3,
              };
              message.error(config);
            }
          }}
          placeholder={" 点击刷新 "}
          style={{ width: "200px" }}
        ></Input>
        <Divider dashed={true} type="vertical"></Divider>
        <Captcha
          charNum={verify_code_default_len}
          fontSize={40}
          width={200}
          height={80}
          bgColor="white"
          onChange={handleClick}
        />
      </Row>
    );
  };

  return (
    <Row
      className="login_bk0"
      style={{
        // backgroundColor: "black",
        width: maxWidth,
        height: maxHeight,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Card
        title={goTORegistPanel()}
        style={{
          width: halfWidth,
          height: halfHeight,
          backgroundColor: "mintcream",
        }}
      >
        {loginView()}
      </Card>
      <ReactCanvasNest
        style={{ backgroundColor: "black" }}
        config={{ lineColor: "255,255,255", pointColor: "153, 238, 221" }}
      />
    </Row>
  );
};
