import React, { useEffect, useState } from "react";
import { Layout, Menu, Row, Typography, Button, message } from "antd";

import * as _ from "lodash";
import SubMenu from "antd/lib/menu/SubMenu";
import { Route, useHistory } from "react-router-dom";
import { global_var } from "../global/userInfo";
import { check_user_login_status } from "./checkStatus";
import { WatchJobInfoPanel } from "./watchJobInfoPanel";
import { FillSelfInfoPanel } from "./fillSelfInfoPanel";
import { PreviewResumeFilePanel } from "./previewResumeFilePanel";
const { Title } = Typography;
const { Header, Content, Footer, Sider } = Layout;

export let MainPanel = () => {
  let [isCollapsed, setIsCollapsed] = useState(false);

  let [jobType, setjobType] = useState("watch_job_info");

  let history = useHistory();

  useEffect(() => {
    let check_res = async () => {
      let res = await check_user_login_status();
      if (res == -1) {
        history.replace("/");
        message.info("未登录");
      }
    };
    check_res();
  }, []);

  useEffect(() => {
    ShowJobPage();
  }, [jobType]);

  let ShowPageRouteControl = () => {
    return (
      <Row>
        <Route path={"/main/watch_job_info/"}>
          <WatchJobInfoPanel />
        </Route>
        <Route path={"/main/fill_self_info/"}>
          <FillSelfInfoPanel />
        </Route>
        <Route path={"/main/preview_resume_file/"}>
          <PreviewResumeFilePanel />
        </Route>
      </Row>
    );
  };

  let ShowJobPage = () => {
    if (_.isEqual("watch_job_info", jobType)) {
      history.replace("/main/watch_job_info/");
    } else if (_.isEqual("fill_self_info", jobType)) {
      history.replace("/main/fill_self_info/");
    } else if (_.isEqual("preview_resume_file", jobType)) {
      history.replace("/main/preview_resume_file/");
    }
    return <Title>未上线</Title>;
  };

  // let ShowJobPage = () => {
  //   if (_.isEqual("watch_job_info", jobType)) {
  //     return <WatchJobInfoPanel />;
  //   } else if (_.isEqual("fill_self_info", jobType)) {
  //     return <FillSelfInfoPanel />;
  //   } else if (_.isEqual("preview_resume_file", jobType)) {
  //     return <PreviewResumeFilePanel />;
  //   }
  //   return <Title>未上线</Title>;
  // };

  return (
    <Layout style={{ opacity: 3 }}>
      <Sider
        style={{
          overflow: "auto",
          height: "100vh",
          left: 0,
        }}
        collapsible={true}
        collapsed={isCollapsed}
        onCollapse={() => {
          setIsCollapsed(!isCollapsed);
        }}
      >
        <Menu
          theme="dark"
          defaultSelectedKeys={["watch_job_info"]}
          mode="inline"
        >
          <Menu.Item
            key="watch_job_info"
            onClick={() => {
              history.replace("/main/watch_job_info/");
            }}
          >
            浏览招聘信息
          </Menu.Item>
          <SubMenu key="self_info" title=" 个人信息 ">
            <Menu.Item
              key="fill_self_info"
              onClick={() => {
                history.replace("/main/fill_self_info/");
              }}
            >
              填写个人信息
            </Menu.Item>
            <Menu.Item
              key="preview_resume_file"
              onClick={() => {
                history.replace("/main/preview_resume_file/");
              }}
            >
              预览简历
            </Menu.Item>
          </SubMenu>
          {/* <Menu.Item key="notification" onClick={() => {}}>
            公告通知
          </Menu.Item>
          <Menu.Item key="about_ours" onClick={() => {}}>
            关于我们
          </Menu.Item> */}
          <Menu.Item
            key="quit_login"
            onClick={() => {
              history.replace("/");
            }}
          >
            退出登录
          </Menu.Item>
        </Menu>
      </Sider>
      <Layout className="site-layout">
        <Header
          style={{
            lineHeight: "20px",
            borderTop: "0px",
            backgroundColor: "orange",
            fontSize: "20px",
            height: "25px",
          }}
        >
          招聘信息网
        </Header>
        {/* <Content style={{ margin: "24px 16px 0", overflow: "initial" }}> */}
        <Content style={{ margin: "0 16px" }}>
          {/* {ShowJobPage()} */}
          {ShowPageRouteControl()}
        </Content>
        <Footer
          style={{
            textAlign: "center",
            fontSize: "20px",
            backgroundColor: "mintcream",
          }}
        >
          resume web site ©2020 Created by kiki
        </Footer>
      </Layout>
    </Layout>
  );
};
