import React, { useEffect, useState } from "react";
import { Button, Col, Divider, Row, Upload, Tag, message } from "antd";
import { InboxOutlined } from "@ant-design/icons";
import * as _ from "lodash";
import {
  get_resume_file_name_api,
  upload_resume_file_api,
} from "../net/netUser";
import { global_var } from "../global/userInfo";
import { PassThrough } from "stream";
import { downloadResByFileName } from "../net/netDownloadFile";
import { nginx_download_url } from "../net/netConfig";
import { useHistory } from "react-router";

const { Dragger } = Upload;

// 可以上传最多三份简历
export let PreviewResumeFilePanel = () => {
  let [resume_data, set_resume_data] = useState([]);
  let [fn, set_fn] = useState("未提交");
  let [f_size, set_f_size] = useState(0);
  let [resume_file, set_resume_file] = useState("");

  let [remote_file_name, set_remote_file_name] = useState("null");

  let history = useHistory();

  let get_file_name = async () => {
    let params = {
      user_uuid: _.get(global_var, "user_info.user_uuid", "null"),
    };
    let data = await get_resume_file_name_api(params);
    console.log("=remote file name", data);
    set_remote_file_name(_.get(data, "data.resume_file_name", "未提交"));
  };

  let maxWidth = document.body.clientWidth;
  let maxHeight = document.body.clientHeight;

  useEffect(() => {
    get_file_name();
  });

  const props = {
    name: "input",
    accept: ".docx,.pdf,.html,.jpg,.png",
    beforeUpload: (file: any, fileList: any) => {
      let async_upload = async () => {
        let file_name = _.get(file, "name", "未提交");
        let file_size = _.get(file, "size", 0);
        set_fn(file_name);
        set_f_size(file_size);
        set_resume_file(file);
        let formData = new FormData();
        formData.set("resume_file_name", file_name);
        formData.set("file", file);
        formData.set(
          "user_uuid",
          _.get(global_var, "user_info.user_uuid", "null")
        );
        await upload_resume_file_api(formData);
        setTimeout(() => {
          get_file_name();
        }, 1000);
      };
      async_upload();
      return false;
    },
    showUploadList: false,
    //   action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
    onChange(info: any) {
      const { status } = info.file;
      if (status !== "uploading") {
        // let fileName = _.get(info, "file.name", "未提交");
        // let fileSize = _.get(info, "file.size", 0);
        // set_fn(fileName);
        // set_f_size(fileSize);
      }
    },
  };

  let file_url =
    nginx_download_url +
    _.get(global_var, "user_info.user_uuid", "") +
    "/" +
    remote_file_name;

  let resume_file_warning_view = () => {
    return (
      <Col>
        <Divider></Divider>
        <Col style={{ fontSize: "larger" }}>简历附件未提交</Col>
        <Col>
          <Button
            type="primary"
            onClick={() => {
              history.replace("/main/fill_self_info/");
            }}
            style={{ fontSize: "larger" }}
          >
            去提交
          </Button>
        </Col>
      </Col>
    );
  };

  let show_preview_panel = () => {
    if (_.isEqual("未提交", remote_file_name)) {
      return resume_file_warning_view();
    } else {
      return (
        <Col style={{ width: "100%" }}>
          <Divider></Divider>
          <Button
            href={file_url}
            target="_blank"
            style={{ fontSize: "larger" }}
            type="link"
          >
            点击此处,打开新网页查看简历
          </Button>
          <Divider></Divider>
          <iframe
            style={{ minWidth: "100%", minHeight: maxHeight }}
            src={file_url}
          ></iframe>
        </Col>
      );
    }
  };

  return (
    <Row gutter={10} justify="center" style={{ width: "100%" }}>
      {show_preview_panel()}
    </Row>
  );
};
