import { global_var } from "../global/userInfo";
import { user_login_api } from "../net/netUser";
import * as _ from "lodash";
import { admin_login_api } from "../net/netAdmin";

export let check_user_login_status = async () => {
  let params = global_var["user_info"];
  let data = await user_login_api(params);
  if (_.isEqual("0", _.get(data, "code"))) {
    return 0;
  }
  return -1;
};

export let check_admin_login_status = async () => {
  let params = global_var["admin_info"];
  let data = await admin_login_api(params);
  if (_.isEqual("0", _.get(data, "code"))) {
    return 0;
  }
  return -1;
};
